<template>
  <div class="tab-tips">
     <el-dialog :title="title+''" v-model="tabTipsVisible" top="30%" :show-close="false">
        <div v-if="tabNname === 0">
            <p>OAEASSRABR</p>
        </div>
        <div v-if="tabNname === 5">
            <p>PTAPTA  </p>
        </div>
        <div v-if="tabNname === 8">
            <p></p>
        </div>
        <div v-if="tabNname === 6">
            <p></p>
        </div>
        <div v-if="tabNname === 7">
            <p></p>
        </div>
        <div v-if="tabNname === 4">
            <p></p>
        </div>
        <div v-if="tabNname === 3">
            <p> </p>
        </div> 
        <div v-if="tabNname === 2">
            <p></p>
        </div>
        <div v-if="tabNname === 1">
            <p></p>
            <ul>
                <li>1</li>
                <li>2112</li>
                <li>3</li>
                <li>4</li>
                <li>5</li>
                <li>6</li>
                <li>7</li>
            </ul>
        </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  name: 'tab-tips',
  data () {
    return {
      tabTipsVisible:false
    }
  },
  props:{
    flag:null,
    tabText:String
  },
  computed:{
    tabNname:function(){
        return this.flag;
    },
    title:function(){
      return this.tabText;
    }
  },
  methods:{
    showTabTips(){
        // this.tabNname = this.flag;
        this.tabTipsVisible = true;
    }
  }
}
</script>

<style lang="less">
.tab-tips{
  .el-dialog{
    width:500px;
    .el-dialog__header{
      text-align: center;
    }
    p,li{
        height:auto;
        line-height:30px;
        font-size: 16px;
        text-indent:2em;
    }
    li{
      font-size: 15px;
    }
  }
}
</style>



