<template>
  <div class="tingli clearfix">
    <!---->
    <ul class="tingliTopTab fl">
        <li :class="{active:checkId === 0}" @click="tagAttogle(0)"></li>
        <li :class="{active:checkId === 1}" @click="tagAttogle(1)"></li>
        <li :class="{active:checkId === 2}" @click="tagAttogle(2)"></li>
        <li :class="{active:checkId === 3}" @click="tagAttogle(3)"></li>
        <li :class="{active:checkId === 4}" @click="tagAttogle(4)"></li>
        <li :class="{active:checkId === 5}" @click="tagAttogle(5)"></li>
        <li :class="{active:checkId === 6}" @click="tagAttogle(6)"></li>
    </ul>
    <div class="ting_warp fl clearfix" v-loading.body="loading" element-loading-text="">
        <div class="tinglileft fl">
            <div class="tinglileftTree">
                <ul>
                    <li v-if="!isActive" @click="_showAddtinglitu"><i class="fl fa fa-file-text-o" style="line-height:35px;margin-left:10px;"></i></li>
                    <li v-else @click="_newupload" ><i class="fl fa fa-file-text-o" style="line-height:35px;margin-left:10px;"></i></li>
                </ul>
                <div class="test-date">
                    <div v-if="!isActive">
                        <el-tree :data="handleData" ref="tree" node-key="id" :props="defaultProps" @node-click="handleNodeClick" :highlight-current="true" :default-expanded-keys="[1]" :default-checked-keys="defaultChecked" :render-content="renderContent"></el-tree>
                    </div>
                <div v-else>
                    <el-tree :data="handleData" ref="tree" node-key="id" :props="defaultProps" :highlight-current="false" :default-expanded-keys="[1]" :default-checked-keys="defaultChecked" @node-click="currentOtherClick" @check-change="currentOtherChecked"></el-tree>
                </div>
                </div>
            </div>
        </div>
        <!--  -->
        <div class="tingliright fl">
            <div v-show="!isActive" class="ear">
                <div v-show="hasData" class="tingli-warp clearfix">
                    <div class="clearfix content-title">
                        <div class="fl xq">
                            
                            <span style="font-size:16px;color:#333;">&nbsp;&nbsp;{{getEar}}</span>
                        </div>
                        <ul class="fr">
                            <!-- <li><i class="el-icon-plus"></i></li> -->
                            <li @click="_deleData('audio_id')"><i class="el-icon-delete2"></i></li>
                            <li @click="_editor"><i class="el-icon-edit"></i></li>
                            <li @click="_download"><i class="fa fa-cloud-download"></i></li>
                            <li @click="_showPrint"><i class="fa fa-print"></i></li>
                        </ul>
                    </div>
                    <div class="svg-show" style="height:calc(100% - 55px);width:100%;">
                        <ele-tinglitu ref="tinglitu" :objZong="canvasData"></ele-tinglitu>
                    </div>
                </div>
                <div  v-show="!hasData" class="noData">
                    <img :src="nodata" alt="" width="300px">
                    <p></p>
                </div> 
            </div>
            <div v-show="isActive" class="tinglirightImg">
                <div v-show="hasData" style="height:100%; margin-left:20px;">
                    <div class="content-title clearfix">
                        <div class="fl xq">
                            
                        </div>
                        <ul class="fr">
                            <li @click="_Rotate"><i class="fa fa-undo"></i></li>
                            <li @click="_deldeImg"><i class="el-icon-delete2"></i></li>
                            <!-- <li @click="_deleData('id')"><i class="el-icon-delete2"></i></li> -->
                            <li @click="upload"><i class="el-icon-edit"></i></li>
                            <li @click="download"><i class="fa fa-cloud-download"></i></li>
                            <li @click="_print"><i class="fa fa-print"></i></li>
                        </ul>
                    </div>
                    <div class="img-preview">
                        <Imgpreview  ref="imgs" v-on:imgs="_Allimgs"></Imgpreview>
                    </div>
                </div>
                <div v-show="!hasData" class="noData">
                    <img :src="nodata" alt="" width="300px">
                    <p></p>
                </div>
            </div>
        </div>
    </div>
    <!--  -->
    <div class="editSvg">
        <el-dialog title="" v-model="addtinglitu" :before-close="cancel" :top="top">
            <addtinglitu ref="addtinglitu" :id="checkedList" v-on:successSave = "_saveSuccess" :objZong="canvasData"></addtinglitu>
        </el-dialog>
    </div>
    <div class="print" v-if="showPrint" >
        <div id="print-tone" :class="{'visibility':hidden}">
            <div id="print-warp">
                <div class="fiexd-w">
                    <div class="print-warp">
                        <div class="print-tone clearfix" id="toimg">
                            <print ref="print" ></print>
                        </div>
                    </div> 
                    <!--  -->
                    <printButton :show="false" @close="closePrint" @code="codeQQ"></printButton>
                </div>
            </div>
        </div>
    </div>
    <div v-if="showQQVisible">
        <codeQQ ref="code" :info="codeInfo" @close="_closeCodeQQ"></codeQQ>
    </div>
  </div>
</template>
<script>
import tinglitu from'./tinglitu'
// import Tinglitu from'./tinglitu - .vue'
import Addtinglitu from'./addAudio.vue'
// import Addtinglitu from'./addAudio - .vue'
import {mapState,mapMutations} from'vuex'
import Imgpreview from '../../commonvue/imgpreview.vue'
import util from '../../../../common/util.js'
import codeQQ from'../../commonvue/codeQQ/codeQQ.vue'
import Print from'../../zuting/report.vue'
import printButton from'../../commonvue/print/print-button.vue'
export default {
  components:{
        'ele-tinglitu':tinglitu,
        Imgpreview,
        Addtinglitu,
        codeQQ,
        Print,
        printButton,
        // Tinglitu
    },
    data(){
      return{
        top:dialog_top,
        addtinglitu:false,
        isActive:false,
        markData: [],
        nodata:Options.nodata,
        handleData:[],
        defaultProps:{
          children: 'children',
          label: 'label'
        },
        // defaultChecked:[1],
        // 
        canvasListData:[],
        // 
        imgsData:[],
        // 
        canvasData:[],
        checkedList:[],
        getEar:'',
        flag:true,
        // 
        imgData:false,
        type:'up',
        time:'',
        id:null,
        file:'',
        hidden:false,
        checkId:0,
        showQQVisible:false,
        showPrint:false,//
        dataArr:[],
        loading:true,
        hasData:true
      }
    },
    // created(){
    //     this.openUrl();
    // },
    beforeRouteEnter (to, from, next) {
        next(function(vm){
            vm.isActive = false;
            sessionStorage.setItem('test_id',JSON.stringify(6));
            vm.time = parseInt(new Date().getTime());//
            vm.$store.commit('getter_code_time',vm.time);
            vm.openUrl();
        })
    },
    beforeRouteLeave(to, from, next) {//
        this.imgsData = [];
        this.canvasListData = [];
        this.handleData = [];
        this.time = null;
        this.hasData = true;
        this.$store.commit('getter_code_time',this.time);
        this.$refs.imgs.serverImgUrl = [];
        this.$refs.imgs.bigImg = null;
        next();
    },
    computed:{
        ...mapState(['studyID','wepAppTime']),
         // 
        defaultChecked:function(){
            console.log(this.handleData)
            if(this.handleData[0]){
                var defaultCheckedId = +this.handleData[0]['children'][0]['id'];
                // 
                // id
                this.checkedList = [defaultCheckedId];
                if(!this.isActive){
                    this._response1(defaultCheckedId,false);
                }else{
                    if(this.imgsData.length > 0){
                        var defaultCheckedId = +this.handleData[0]['children'][0]['id'];
                        this.checkedList = [defaultCheckedId];
                        this.$refs.imgs.getStudyImg('/audiogram/otherimages',{'id':defaultCheckedId});
                    };
                    return [defaultCheckedId];
                };
                return [defaultCheckedId];
            }
            return [0];
        } ,
        codeInfo:function(){//
            return {'time':this.time,'type':this.type,'user_id':this.studyID,'file':this.file,'id':this.id,'imgs_id':this.checkId}
        },
    },
    watch:{
        wepAppTime:function(){   
            let time = this.time
            if(this.wskt.up_loder(this) === 'redact'){
                // this.$refs.code.dialogQQVisible = false;//
                this._closeCodeQQ(false)
                if(this.id){//
                    this.$refs.imgs.getStudyImg('/audiogram/otherimages',{'id':this.checkedList[0]});
                }else{//
                    this.tagAttogle(this.checkId);
                } 
            }
        },
    },
    methods:{
        _print(){//
            let that = this;
            util.getPrint(this,next);
            function next(){
                // util.print('#img-print');
                that.$refs.imgs.print()
            }
        },
        // 
        _download(){
            //
            this.time = parseInt(new Date().getTime());
            this.$store.commit('getter_code_time',this.time);
            util.getterIpCode(this,next);
            function next(pointer){
                function prom(pointer){
                    return new Promise((resolve,reject) =>{
                        pointer.$store.commit('code_api','bg');
                        pointer.showPrint = true;
                        pointer.hidden = true;//
                        resolve(pointer)
                    })
                }
                prom(pointer).then((pointer) =>{
                    return new Promise((resolve,reject) =>{
                        pointer.$refs.print.createdPng();
                        pointer.$refs.print._lookPng();
                        resolve(pointer)
                    })
                }).then((pointer) =>{
                    return new Promise((resolve,reject) =>{
                        pointer.file = 'ceshi';
                        pointer.type = null;
                        pointer.showQQVisible = true;
                        resolve(pointer)
                    })
                }).then((pointer) =>{
                    return new Promise((resolve,reject) =>{
                        pointer.$refs.code.useqrcode();
                        resolve(pointer)
                    })
                }).catch((err)=>{
                    console.log(err)
                })
            };
        },
        // 
        upload(){
            //
            this.time = parseInt(new Date().getTime());
            this.$store.commit('getter_code_time',this.time);
            util.getterIpCode(this,next);
            function next(pointer){
                pointer.$store.commit('code_api','up');
                pointer.showQQVisible = true;
                pointer.type = "up";
                pointer.id = pointer.checkedList[0];
                pointer.file = 'tingli';
                setTimeout(() =>{
                    pointer.$refs.code.useqrcode();
                },50)
            };
        },
        cancel(){//
            this.addtinglitu = false;
            this.$refs.addtinglitu.Close();
        },
        // 
        _saveSuccess(e){
            if(e){//
                this.cancel();//
                this.openUrl();
            }
        },
        _editor(){
            this.addtinglitu = true;
            // 
            setTimeout(()=>{
                this.$refs.addtinglitu.add = false;
                // this.$refs.addtinglitu._editor_drawyuan();
                this.$refs.addtinglitu.editor();
            },50);
        },
        _newupload(){//
            //
            this.time = parseInt(new Date().getTime());
            this.$store.commit('getter_code_time',this.time);
            util.getterIpCode(this,next);
            function next(pointer){
                pointer.$store.commit('code_api','up');
                pointer.type = "up";
                pointer.id = null;
                pointer.file = 'tingli';
                pointer.showQQVisible = true;
                setTimeout(() =>{
                    pointer.$refs.code.useqrcode();
                },50)
            };
        },
        //
        download(){
            //
            this.time = parseInt(new Date().getTime());
            this.$store.commit('getter_code_time',this.time);
            util.getterIpCode(this,next);
            function next(pointer){
                pointer.$store.commit('code_api','dw');
                pointer.showQQVisible = true;
                pointer.type = "down";
                pointer.file = 'tingli';
                pointer.id = pointer.checkedList[0];
                setTimeout(() =>{
                    pointer.$refs.code.useqrcode();
                },50)
            };
        },
        _showAddtinglitu(){
            this.addtinglitu = true;
        },
        // 
        tagAttogle(index){
            this.loading = true;
            this.handleData = [];
            this.checkId = index;
            if(index !== 0){
                this.isActive = true;
                let user_id = {user_id:this.studyID,imgs_id:index};
                this.$ajax.post('/audiogram/otherlist',user_id).then((response)=>{
                    if(response.code === 200){
                        this.imgsData = response.data;
                        this._handleCanvasData(this.imgsData,'id');
                        this.imgsData.length > 0 ? this.hasData = true : this.hasData = false;
                    }else{
                        this.imgsData = [];
                        this.hasData = false;
                    }
                    this.loading = false;
                });
            }else{//
                this.isActive = false;
                this.openUrl();
            };
        },
        _Allimgs(data){
            if(data.length == 0){//
                let agruments = {
                      'id':this.checkedList,
                      'user_id':this.studyID
                    };
                let url = '/audiogram/otherdelete';
                // 
                this._api(this,url,agruments,true,'id',true);
            };
        },
        // 
        openUrl(){
            this.$ajax.post('/audiogram/showlist',{user_id:this.studyID}).then((response)=>{
                console.log(response)
                if(response.code === 200){
                    this.canvasListData = response.data;
                    // this._response1(response.data[0].audio_id,false)
                    this._handleCanvasData(this.canvasListData,'audio_id');
                     this.canvasListData.length > 0 ? this.hasData = true : this.hasData = false;
                }else{//
                    this.canvasData = ['',''];
                    this.dataArr = ['',''];
                    this.canvasListData = [];
                    this.hasData = true;
                }
                this.loading = false;
            });
        },
        contrast(store, data) {
            this._response1(data['id'],true)
            event.stopPropagation();
        },
        renderContent(h, { node, data, store }) {
            return (
              <span class="con-btn">
                <span>
                  <span>{node.label}</span>
                </span>
                <span style="float: right;margin-right: 10px;" on-click={ () => this.contrast(store, data) }>
                  <span></span>
                  <el-button size="mini"></el-button>
                </span>
              </span>);
        },
        // 
        _handleCanvasData(e,id){
            let str = e;
            let data = [];
            str.forEach((item) =>{
                // 
                let getTime = new Date(item['time']);
                let labelTime = (+getTime.getMonth() + 1) +'' +getTime.getDate()+'';
                let parentTime = getTime.getFullYear()+'';
                // 
                let year = "";
                if(!item.project_name){
                    for (let i = 0; i < data.length; i++) {
                        year += data[i]['label'] ;
                    };
                    if(year.indexOf(parentTime) > -1){
                        for (let i = 0; i < data.length; i++) {
                            // 
                            if(data[i]['label']  === parentTime) {
                                data[i]['children'].push({'label':labelTime,'id':item[id]});
                            }
                        }
                    }else{//push
                        let obj = {
                            "label":parentTime,
                            'children':[{'label':labelTime,'id':item[id]}]
                        }
                        data.push(obj)
                    }
                }
            });
            this.handleData = data;
        },
        // 
        _response1(id,flag){
            this.$ajax.post('/audiogram/showone',{'audio_id':id}).then((response) =>{
                // console.log(response)
                if(response.code === 200){
                    const resdata = response.data,
                        svgData = JSON.parse(resdata.data);
                    // this.$store.commit('hasCanvasData',resdata);
                    // let user_text = JSON.stringify(resdata);
                    // sessionStorage.setItem('user_text',user_text)
                    this.getEar = resdata.radioEar;
                    if(flag){
                        if(this.canvasData.length > 1){//
                            this.canvasData.shift();
                            this.dataArr.shift();
                        };
                        this.canvasData.unshift(svgData);
                        this.dataArr.unshift(resdata);
                    }else{
                        this.dataArr = [resdata];
                        this.canvasData = [svgData];
                    };
                    let vuexdata  = this.dataArr[this.dataArr.length-1];
                    this.$store.commit('hasCanvasData',vuexdata);
                    let user_text = JSON.stringify(vuexdata);
                    sessionStorage.setItem('user_text',user_text);
                    setTimeout(() =>{
                         this.$refs.tinglitu.drawyuan();//svg
                     },50)
                }
            })
        },
        handleNodeClick(data,node,str) {
            if(data.id === undefined){return;}
            this.checkedList = [data.id]
            this.checkedListPre = data.id;
            // 
            this.$refs.tree.setCheckedKeys([data.id]);
            // 
            this._response1(data['id'],false)
        },
        // 
        currentOtherClick(data){
            console.log(data,data.id);
            if(data.id === undefined){return;}
            // 
            this.$refs.tree.setCheckedKeys([data.id]);
        },
        currentOtherChecked(data,checked,node){
            console.log(data.id,checked,node);
            // this.$refs.tree.setCheckedKeys([data.id]);
             if(checked){
                if(data.id === undefined){return;}
                this.checkedList = [data.id];
                this.$refs.tree.setCheckedKeys([data.id]);//
                // console.log(111);
                this.$refs.imgs.getStudyImg('/audiogram/otherimages',{'id':data.id});
             }
        },
        // 
        _deldeImg(){
            this.$refs.imgs._deldeImg('/audiogram/deleteimage',this.checkedList[0]);
        },
        // 
        _deleData(id){
            // util._click(this,deleData);
            deleData(this);
            function deleData(that){
                if(that.checkedList.length <= 0){msgTipsErr(that,'');return;}
                // if(that.checked.length>0){
                that.$confirm('', '', {
                        confirmButtonText: '',
                        cancelButtonText: '',
                        type: 'warning'
                    }).then(() =>{
                        // 
                       let agruments,url;
                       if(id !== 'id'){
                            agruments = {
                              'audio_id':that.checkedList,
                              'user_id':that.studyID
                            };
                            url = '/audiogram/delete';
                       }else{
                            agruments = {
                              'id':that.checkedList,
                              'user_id':that.studyID
                            };
                            url = '/audiogram/otherdelete';
                       }
                       console.log(agruments)
                        // 
                        that._api(that,url,agruments,true,id);
                    }).catch(() =>{
                        msgTipsSuccess(that,'!'); 
                        return;
                    });
            };
        },
        // api
        _api(str,api,agruments,dele,id,flag){
            // console.log(id)
            str.$ajax.post(api,agruments).then((response) =>{
                console.log(response);
                if (response.code ===200){
                    let markData = response.data;
                    if(response.data.length == 0){//
                        if(id == 'id'){
                            str.imgsData = [];
                        }else{
                            str.canvasListData = [];
                        }
                    };
                    if(!flag){//
                       msgTipsSuccess(str,''); 
                    };
                    str._handleCanvasData(markData,id)
                }else{
                    msgTipsErr(str,'');
                    str.markData = [];
                };
                  // 
                if(dele){
                    str.checkedList.length = 0;
                };
            }).catch((err) =>{
                  msgTipsErr(str,'');
            })
        },
        // 
        _Rotate(){
            this.$refs.imgs._Rotate();
        },
        closePrint(e){//
          this.showPrint = e;
        },
        _closeCodeQQ(e){//
          this.showPrint = e;
          this.showQQVisible = false;
          this.hidden = e;
          // this.$store.commit('getter_code_time','');
        },
        codeQQ(e){
            this.$store.commit('code_api','bg');
            // 
            this.file = 'ceshi';
            this.type = null;
            this.showQQVisible = true;
            setTimeout(() =>{
                this.$refs.code.useqrcode();
            },50)
        },
        //
        _showPrint(){
            this.showPrint = !this.showPrint;
            setTimeout(()=>{//
                this.$refs.print.createdPng();
                // this.$refs.print._lookPng();
            });
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
@titleH:50px;
@border:#88d89f;
@contitle:35px;
@fc:#fff;
@bcl:#65c0df;
.tingli{
    padding-left:20px;
    /************/ 
    .noData{
        ul>li{
            font-size: 20px;
             background-color: @bcl;
              width: 150px;
              height: 50px;
              margin:8px auto;
              line-height: 50px;
              text-align: center;
              margin-top: 10px;
              border-radius: 5px;
              cursor:pointer;
              color:#fff;
              // &:hover{
              //   background-color:#25C707;
              // }
        }
    }
    /*****************/ 
    .tinglileft{
        margin-top: 30px;
        background:@bcl;
        border:3px solid @bcl;
        border-radius: 0 13px 13px 13px;
        box-shadow:1px 1px 5px @bcl;
        .tinglileftTree{
            width:220px;
            height:500px;
            ul{
                background-color:#fff;
                padding-top: 5px;
                padding-bottom: 5px;
                border-radius:0 10px 0 0;
            }
            li{
                font-size:20px;
                line-height: @contitle;
                margin:0px 10px;
                height:@contitle;
                text-align:center;
                border-radius:5px;
                color:@fc;
                border:1px solid #278bc4;
                background-color: #278bc4;
                cursor:pointer;
                box-shadow: 2px 2px 2px #ccc;
            }
            .el-tree-node__content{
                height: 40px;
                line-height: 40px;
            }
            .con-btn .el-button{
                font-size: 16px;
                padding: 8px;
                min-width: 60px;
            }
            .con-btn .el-button:focus{
                background: #1ab297;color:#FFF;
            }
            .test-date{
                overflow-y: auto;
                height: 453px;
                background-color:#fff;
                border-radius:0 0 10px 10px;
                .el-tree{
                  border:0px none;
                }
            }
        }
    }
    .tingliTopTab{
        padding-top:30px;
        li{
            cursor:pointer;
            background-color: #eee;
            width: 110px;
            text-align: center;
            height: 40px;
            line-height: 40px;
            color: #337ab7;
            margin-bottom: 10px;
            font-size: 18px;
            border-radius: 5px 0 0 5px;
            box-shadow: 0px 2px 2px #ccc;
            &.active{
                background-color: @bcl;
                color:@fc;;
            }
        }
    }
    /*****************/ 
    .tingliright{
        padding-top:30px;
        // width:900px;
        height:calc(~"100% - 30px");
        box-sizing: border-box;
        .ear{
            width:100%;
            height: 100%;
        }
        .tingli-warp{
            margin-left:20px;
            height:100%;
        }
        .xq{line-height:47px; font-size:18px; text-indent:1em;}
        .content-title{
            width:100%;
        }
        /**********/ 
        .tinglirightImg{
            height: 100%;
            position: relative;
            .img-preview{
                height: calc(~"100% - 52px");
            }
            .img .topBigimg img{
                max-width:450px;
            }
            .QQcode{
                position: absolute;
                right:42px;
                top:130px;
                width:120px;
                height:150px;
                // background-color:red;
                .QQcodeinner{
                    height:130px;
                    width:120px;
                    background-color: #37c438;
                    border-radius:10px;
                    position: absolute;
                    left:50%;
                    top:20px;
                    transform:translate(-50%);color:@fc;
                    text-align:center;
                    div{
                        width:90px;
                        height:90px;
                        background-color: #fff;
                        margin-left:15px;
                        margin-top:15px;

                    }
                }
                p{
                    margin-top:150px;
                    color:#37c438;
                    font-size: 14px;
                }
                ul>li{
                     background-color: @bcl;
                      width: 120px;
                      height: 35px;
                      margin-left:8px;
                      line-height: 35px;
                      text-align: center;
                      margin-top: 15px;
                      border-radius: 5px;
                      cursor:pointer;
                      color:#fff;
                      // &:hover{
                      //   background-color:#25C707;
                      // }
                }
            }
        }
    }
    .editSvg{
        .el-dialog{
            padding-bottom:30px;
            width:1300px;
        }
    }
}
</style>



