import Vue from 'vue'
import Router from 'vue-router'
// import Hello from '@/components/Hello'
// 
import Base from '@/components/home/danganlist/base/base'
// import Gametest from '@/components/home/danganlist/gametest/gametest'
// import Ystest from '@/components/home/danganlist/ystest/ystest'
// import Noise from '@/components/home/danganlist/noise/noise'
// import Eight from '@/components/home/danganlist/eight/eight'
// import Tone from '@/components/home/danganlist/tone/tone'
// import Personality from '@/components/home/danganlist/personality/personality'
import Login from'@/components/login/login'
import danganlist from '@/components/home/danganlist/danganlist'
import Home from '@/components/home'
import bingli from '@/components/home/danganlist/bingli/bingli'
import pinggu from '@/components/home/danganlist/pinggu/pinggu'
import tingli from '@/components/home/danganlist/tingli/tingli'
// 
import Archives from '@/components/home/danganlist/archives'
// 
import More from '@/components/more'
import zuTing from '@/components/home/zuting/header-info'
import zuTest from '@/components/home/zuting/params'
import zuGameTest from '@/components/home/zuting/gamesTest'
import audiometry from '@/components/home/zuting/audiometry'
// 
import liuTest from'@/components/home/linsly/params'
import liuGameTest from'@/components/home/linsly/gamesTest'
import liuAudiometry from'@/components/home/linsly/audiometry'
// 
import ToneTest from'@/components/home/tone/params'
import ToneGameTest from'@/components/home/tone/gamesTest'
import ToneAudiometry from'@/components/home/tone/audiometry'
// 
import Especial from'@/components/home/especial/setterParams'
import EspecialTest from'@/components/home/especial/params'
import EspecialAudiometry from'@/components/home/especial/audiometry'
import EspecialGameTest from'@/components/home/especial/gamesTest'
// 
import MoreHigh from'@/components/home/morehigh/morehight'
// 
import Statistic from'@/components/home/statistic/statistic'
Vue.use(Router)

export default new Router({
  // mode:'history',
  routes: [
  // 
    {
      path: '*',
      redirect:'/home'
    },
    {
      path: '/login',
      component : Login
    },
    {
      path: '/home',
      component : Home,
      name:'',
     },
     {
      path: '/more',
      component : More,
      name:'',
     },
     {
      path: '/home/Archives',
      // component : danganlist,
      component : Archives,
      // redirect:'/home/Archives/danganlist/base',
      name:'',
      children:[
        {
          path:'/home/Archives/dalist',
          component:danganlist,
          // redirect:'/home/Archives/danganlist/base',
          name:'',
          children:[
          {path:'/home/Archives/dalist/base',component:Base,name:''},
          {path:'/home/Archives/dalist/bingli',component:bingli,name:''},
          {path:'/home/Archives/dalist/tingli',component:tingli,name:''},
          {path:'/home/Archives/dalist/pinggu',component:pinggu,name:''},
          ]
        },{
          path:'/home/Archives/mh',
          component:MoreHigh,
          name:''
        },{
          path:'/home/Archives/fx',
          component:Statistic,
          name:''
        }
      ],
      // children:[
      //   {path:'/home/Archives/danganlist/base',component:Base,name:''},
      //   {path:'/home/Archives/danganlist/bingli',component:bingli,name:''},
      //   {path:'/home/Archives/danganlist/tingli',component:tingli,name:''},
      //   {path:'/home/Archives/danganlist/pinggu',component:pinggu,name:''},
      // ]
    },
    {
      path: '/home/zhuting',
      component : zuTing,
      redirect:'/home/zhuting/base',
      name:'',
      children:[
        {path:'/home/zhuting/base',component:zuTest,name:''},
        {path:'/home/zhuting/audiometry',component:audiometry,name:''},
        {path:'/home/zhuting/train',component:zuGameTest,name:''},
      ]
    },
    {
      path: '/home/linsix',
      component : zuTing,
      redirect:'/home/linsix/base',
      name:'',
      children:[
        {path:'/home/linsix/base',component:liuTest,name:''},
        {path:'/home/linsix/audiometry',component:liuAudiometry,name:''},
        {path:'/home/linsix/train',component:liuGameTest,name:''},
      ]
    },
    {
      path: '/home/hseight',
      component : zuTing,
      redirect:'/home/hseight/base',
      name:'',
      children:[
        {path:'/home/hseight/base',component:ToneTest,name:''},
        {path:'/home/hseight/audiometry',component:ToneAudiometry,name:''},
        {path:'/home/hseight/train',component:ToneGameTest,name:''},
      ]
    },
    {
      path: '/home/especial',
      component : zuTing,
      redirect:'/home/especial/baseA',
      name:'',
      children:[
        {path:'/home/especial/baseA',component:Especial,name:''},
        {path:'/home/especial/base',component:EspecialTest,name:''},
        {path:'/home/especial/audiometry',component:EspecialAudiometry,name:''},
        {path:'/home/especial/train',component:EspecialGameTest,name:''},
      ]
    },
    {
      path: '/home/tone',
      component : zuTing,
      redirect:'/home/tone/base',
      name:'',
      children:[
        {path:'/home/tone/base',component:ToneTest,name:''},
        {path:'/home/tone/audiometry',component:ToneAudiometry,name:''},
        {path:'/home/tone/train',component:ToneGameTest,name:''},
      ]
    },
    {
      path: '/home/syllable',
      component : zuTing,
      redirect:'/home/syllable/base',
      name:'',
      children:[
        {path:'/home/syllable/base',component:ToneTest,name:''},
        {path:'/home/syllable/audiometry',component:ToneAudiometry,name:''},
        {path:'/home/syllable/train',component:ToneGameTest,name:''},
      ]
    },
  ]

})



