<template>
	<div class="jigou">
		<div>
			<el-dialog title="" v-model="dialogTableVisible" @close="resetForm('formJigou')">
				<el-form :label-position="labelPosition" label-width="80px" :model="formJigou" ref="formJigou" :rules="rulesJigou">
				  	<el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-bank"></i> :
					  	</el-col>
					  	<el-col :span="18">
					  		<el-form-item  prop="name">
					  			<el-input v-model="formJigou.name" placeholder="">{{formJigou.name}}</el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
					<el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-home"></i> :
					  	</el-col>
					  	<el-col :span="18">
					  		<el-form-item  prop="full_name">
					  			<el-input v-model="formJigou.full_name" placeholder=""></el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
				    <el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-map-marker"></i> :
					  	</el-col>
					  	<el-col :span="18">
					  		<el-form-item  prop="address">
					  			<el-input v-model="formJigou.address" placeholder=""></el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
				    <el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-phone"></i> :
					  	</el-col>
					  	<el-col :span="7">
					  		<el-form-item  prop="phone">
					  			<el-input v-model="formJigou.phone" placeholder="12123"></el-input>
					  		</el-form-item>
					  	</el-col>
					  	<el-col :span="4">
					  		<i class="glyphicon glyphicon-phone"></i> :
					  	</el-col>
					  	<el-col :span="7">
					  		<el-form-item  prop="cellphone">
					  			<el-input v-model="formJigou.cellphone" placeholder="123456789"></el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
				    <el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-qq"></i>QQ :
					  	</el-col>
					  	<el-col :span="7">
					  		<el-form-item  prop="qq">
					  			<el-input v-model="formJigou.qq" placeholder="12123"></el-input>
					  		</el-form-item>
					  	</el-col>
					  	<el-col :span="4">
					  		<i class="fa fa-wechat"></i> :
					  	</el-col>
					  	<el-col :span="7">
					  		<el-form-item  prop="wechat">
					  			<el-input v-model="formJigou.wx" placeholder="123456789"></el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
				    <el-row type="flex" :gutter="5" align="middle">
					  	<el-col :span="6">
					  		<i class="fa fa-envelope"></i>Email :
					  	</el-col>
					  	<el-col :span="7">
					  		<el-form-item  prop="email">
					  			<el-input v-model="formJigou.email" placeholder="12123@qq.com"></el-input>
					  		</el-form-item>
					  	</el-col>
					  	<el-col :span="4">
					  		<i class="fa fa-globe"></i> :
					  	</el-col>
					  	<el-col :span="7">
						  	<el-form-item prop="website">
						  		<el-input v-model="formJigou.website" placeholder="http://www."></el-input>
					  		</el-form-item>
					  	</el-col>
				    </el-row>
				</el-form>
				<div slot="footer" class="dialog-footer">
				    <el-button type="primary" @click.native="submitJigouForm('formJigou')"> </el-button>
				</div>
			</el-dialog>
		</div>
	</div>
</template>
<script>
import {jgshow,jgupdate} from '../../../api/api'
export default {
  	data() {
  		var validatePass = (rule, value, callback) => {
	  		var reg = /^1[3578]\d{9}$/;
	        if (!reg.test(value)) {
	          callback(new Error(''));
	        }else{
	        	callback();
	        }
	    };
	    var validatePhone = (rule, value, callback) => {
	  		var reg = /^0\d{2}-\d{8}$|^0\d{3}-\d{7}$/;
	        if (!reg.test(value)) {
	          callback(new Error(' '));
	        }else{
	        	callback();
	        }
	    };
      	return {
      		uploadData:{"logo":''},
      		port:port,
	       	labelPosition: 'right',
	       	web_url:web_url,
	        data:{
	        	logo:''
	        },
	        formJigou: {
	        },
	        src:'',
	        imageUrl: '',
	        dialogTableVisible: false,
	        rulesJigou:{
	        	name: [
	                { min: 1, max: 50, message: ' 1  50 ',trigger: 'blur'}
	            ],
	            full_name: [
	                { min: 1, max: 20, message: ' 1  20 ',trigger: 'blur'}
	            ],
	        	phone: [
	                { message: '', trigger: 'blur' },
	                // { validator: validatePhone, trigger: 'blur' },
	                { pattern:/^0\d{2}-\d{8}$|^0\d{3}-\d{7}$/, message: ' ',trigger: 'blur'},
	                { min: 10, max: 12, message: '12', trigger: 'blur'}
	              ],
	             cellphone: [
	                  { message: '', trigger: 'blur' },
	                  // { validator: validatePass, trigger: 'blur' },
	                  { pattern:/^1[3578]\d{9}$/, message: '',trigger: 'blur'},
	                  { min: 11, max: 11, message: '11', trigger: 'blur'}
	              ],
	            qq: [
	                  {  message: 'QQ', trigger: 'blur' },
	                  { pattern:/^[1-9][0-9]+$/, message: 'QQ'},
	                  { min: 3, max: 12, message: ' 3  12 '}
	              ],
	            wechat: [
	                  {  message: '', trigger: 'blur' },
	                  { min: 1, max: 20, message: ' 1  20 '}
	              ],
	            email:[
	            	{ pattern:/^\w+@\w{2,5}(\.[a-z]+){1,2}$/i, message: 'Email', trigger: 'blur'}
	            ],
	            website:[
	            	{ pattern:/^([hH][tT]{2}[pP]:\/\/|[hH][tT]{2}[pP][sS]:\/\/)(([A-Za-z0-9-~]+)\.)+([A-Za-z0-9-~\/])+$/, message: 'http://', trigger: 'blur'},
	            	{ min: 5, max: 50, message: ' 1  50 ',trigger: 'blur'}
	            ]
	        }
	    }
	},
	mounted (){
	},
	methods:{
		jigouInfo(){
			var that = this,
				institution = {institution:sessionStorage.getItem('user')}
			this.$ajax.post(`/operator/jgshow`,institution).then(function(response){
				if(response.code === 200) {
					that.formJigou = response.data;
				} else{
					that.msgTipsSuccess(this,'')
				}
			}).catch((err)=>{
				alert(err)
			})
		},
		upLoad(){
			// 
			this.$refs.upload.show()
		},
		showTable:function (){
		// 
			this.dialogTableVisible = true
		},
	    resetForm(formName) {
            this.$refs[formName].resetFields();
        },
		submitJigouForm(str){
			console.log(this.formJigou)
			var that = this
			this.$refs[str].validate((valid) => {
	          if (valid) {
	          	this.$ajax.post(`/operator/jgupdate`,this.formJigou).then(function(response){
	          		console.log(response);
	          		if(response.code == 200) {
						that.formJigou = response.data;
						that.dialogTableVisible = false;
						msgTipsSuccess(that,'')
					} else{
						msgTipsErr(that,'')
					}
	          	}).catch(function(error){
	          		console.log(error)
	          		alert(error);
	          	})
	          } else {
	            alert('!');
	            return false;
	          }
        	});
		}

	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang ="less">
	.jigou{
		.el-dialog__body{
			padding:10px 45px;
		}
		/**/
		.el-dialog__header{
			padding:0px 20px 0px 20px;
			/* padding-left:20px;*/
			line-height: 40px;
			background-color: #f8f8f8;
			border-bottom:1px solid #ccc;
		}
		.el-form-item{
			margin: 0px;
		}
		.el-form-item__content{margin:0px !important;}
		/*inputlabel*/
		.el-col-6,.el-col-4{
			text-align: right;
			padding-right: 20px!important;
			font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
			font-weight: 700;
			font-size: 14px;
			color:#333;
		}
		/*input*/
		.el-row--flex.is-align-middle{
			margin:20px 0px;
		}
		/**/
		.dialog-footer{
			padding-right:23px;
			padding-bottom: 10px;
		}
	}
	.v-modal{
		z-index: 2000!important;
	}
</style>



