<template>
	<div class="password">
		<el-dialog title="" v-model="dialogFormVisible"  @close="resetForm('formPass')">
		  <el-form :model="formPass" :rules="passRules" ref="formPass" class="demo-ruleForm">
			<el-row>
				<el-form-item label=":" prop="yspassword">
			  		<el-input type="password" v-model="formPass.yspassword"></el-input>
			  	</el-form-item>
		    </el-row>
			<el-row v-bind:class="{'errorClass':passShow}">
				<el-form-item label="" prop="password">
			  		<el-input type="password" v-model="formPass.password" auto-complete="off"></el-input>
			  	</el-form-item>
			  	
		    </el-row>
			<el-row v-bind:class="{'errorClass':checkShow}">
				<el-form-item label="" prop="checkPass">
			  	<el-input type="password" v-model="formPass.checkPass" auto-complete="off"></el-input>
			  	</el-form-item>
		    </el-row>   
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button type="primary" @click="submitForm('formPass')"> </el-button>
		  </div>
		</el-dialog>
		
	</div>
  
</template>
<script>
import {uppwd,showStudentInfo} from '../../../api/api'
export default {
  data() {
      var validatePass2 = (rule, value, callback) => {
        if (value === '') {
        	this.checkShow = true;
          callback(new Error(''));
        } else if (value !== this.formPass.password) {
          callback(new Error('!'));
          // this.checkPass = true;
          // this.checkShow = false;
        } else {
	        // this.checkShow = false;
	        callback();
        }
      };
  	return {
  		dialogFormVisible: false,
        formPass: {
          yspassword: '',
          password:'',
          checkPass : ''
        },
        formLabelWidth: '120px',
        // 
        passRules:{
        	yspassword:[
        		{required: true,message: '',  trigger: 'blur'},
        		{ min: 6, max: 30, message: ' 6  30 '},
        		{ pattern:/^[a-zA-Z0-9_\.]+$/, message: ''}
        	],
        	password :[
        		{required: true,message: '',  trigger: 'blur'},
        		{ min: 6, max: 30, message: ' 6  30 '},
        		{ pattern:/^[a-zA-Z0-9_\.]+$/, message: ''}
        	],
        	checkPass: [
	            {required: true, validator: validatePass2, trigger: 'blur' },
	            { min: 6, max: 30, message: ' 6  30 '}
	        ]
        },
        checkShow : false,
        passShow : false
        // checkPass:false
  	};
  },
  props:{
  	pwd:null
  },
  methods : {
  	showTable(){
  		this.dialogFormVisible = true;
  	},
  	//
    resetForm(formName) {
        this.$refs[formName].resetFields();
    },
  	// 
  	submitForm(formName) {
  		// console.log(this.$refs[formName]);
  		var that = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            var updata = {
	        	operator_id : this.pwd,
	        	yspassword : this.formPass.yspassword,
	        	password : this.formPass.password
	        };
			uppwd(updata).then(function(response){
		        	console.log(response);
		        	if(response.msg == ""){
		        		msgTipsSuccess(that,',!');
		        		setTimeout(()=>{
		        			that.$router.push('/login');
		        		},1500);
		        	}else{
		        		msgTipsErr(that,'!');
		        	}
		        }).catch(function(error){
		            console.log(error);
		        })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
        
      }
  	}
}

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang ="less">
	.password{
		.el-dialog__body{
			padding:10px 20px;
		}
		.el-dialog__header{
			padding:0px 20px 0px 20px;
			// padding-left:20px;
			line-height: 40px;
			background-color: #f8f8f8;
			border-bottom:1px solid #ccc;
		}
		.el-form-item{
			margin: 0px;
		}
		/*.el-row{
			margin-top: 6px;
			margin-left:5px;
		}*/
		.el-dialog{
			width: 400px;
			height: 400px;
		}
		.el-dialog__footer{
			padding:0px 20px 0px;
		}
		.el-form-item__content{
			margin-bottom: 15px;
		}
		/**/
		/*.el-form-item__error{
			top: 80px;
    		left: 7px;
		}*/
		/*.errorClass{
			.el-input__inner{
				border:1px solid red;
			}
			.tips{
				color:red;
			}
		}*/
	}
</style>



