//vuevuex
import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex);
//gettersmutationsactions
import getters from './getters'
import mutations from './mutations'
import actions from './actions'
//state
const state = {
	// 
	initial_value:{},
    zhutingData : {},
    voiceData : {},
    hz:[],
    // maxDbList:[],
    // 
    progress:[],
    // 
    voicesGroup :{},
    // id
    studyID:'',
    baseActive:'',
    wepAppTime:'',//
    canvasMarks:'',
    CMDisOnline:{},
    time:'',//
    test_spl:'',//
    currenttonetype:'',//,
    codeApi:'',
    IP:null,
    printMsg:null,
    allPrintImgs:[],
    hadDeleStudy:[],
    product:[],//
    gamesTimesObj:{},
    btn_tips:false//
}
// console.log(state)
// console.log(Vuex)
//store
export default new Vuex.Store({
    state,
    getters,
    mutations,
    actions
})



