let testTypeArr = [{name:'',key:6},{name:'',key:1},{name:'',key:2},{name:'',key:3},{name:'',key:4}];
const boy = require('./images/body.png');
const girl = require('./images/girl.png');
const rank_1 = require('./images/rank_1.gif');
const rank_2 = require('./images/rank_2.gif');
const rank_3 = require('./images/rank_3.gif');
const rank_4 = require('./images/rank_4.gif');
const nodata = require('./images/nodata.png');
export default {
    deaf_degree :[{name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}],
    // 
    deaf_nature :[{name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}],
    // 
    testType :testTypeArr,
    testType1 :testTypeArr.concat([{name:'',key:7}]),
    // 
    optionsEffect :[{name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}],
    optionsDegree :[{name: '',label: ''}, {name: '',label: ''}, {name: '',label: ''}],
    optionsTestModel :[{name:'BOA',label:'BOA'},{name:'VAR',label:'VAR'},{name:'PA',label:'PA'},{name:'PTA',label:'PTA'}],
    optionsTone :[{name:'',label:''},{name:'',label:''}],
    PTA : [{value: '',label: ''},{value: '',label: ''},{value: '',label: ''},{value: '',label: ''},{value: '',label: ''}],
    typeObject:(function(){
        let arr = testTypeArr,
            obj ={progress:0,overCount:0},
            res = [];
        arr.forEach(item =>{
            res.push(Object.assign(item,obj))
        })
        return res;
    })(),
    boy: boy,
    girl: girl,
    rank_1: rank_1,
    rank_2: rank_2,
    rank_3: rank_3,
    rank_4: rank_4,
    nodata: nodata,

}


