<template>
    <header class="print-title">
      <h2>{{current_gameName}}</h2>
      <ul class="clearfix">
        <li  class="li-name">
          <span></span>
          <div>{{user_Info['name']}}</div>
        </li>
        <li class="li-in">
          <span></span>
          <div>{{user_Info['gender']}}</div>
        </li>
        <li>
          <span></span>
          <div>{{user_Info['birth']}}</div>
        </li>
        <li>
          <span></span>
          <div v-if="current_gameName == ''">{{getTime}}</div>
          <div v-else>{{info['time']}}</div>
        </li>
      </ul>
      <div class="sb clearfix">
        <p class="fl"></p>
        <span class="fl">{{userInfo.eq_left}}</span>
        <span class="fl sb-r">{{userInfo.eq_right}}</span>
      </div>
      <div v-if="current_gameName == ''" class="ear zu clearfix">
        <p class="fl"><span>{{canvasMarks.testmodel}}</span></p>
        <p class="fl"><span>{{canvasMarks.getmodel}}</span></p>
        <p class="fl"><span>{{canvasMarks.degree}}</span></p>
        <p class="fl"><span>{{canvasMarks.radioEar}}</span></p>
        <p class="fl"><span>{{currenttonetype.tonetype}}</span></p>
      </div>
      <div v-else class="ear clearfix">
        <p class="fl">{{info['ear']}}</p>
        <p class="fl">{{info['environment']}}</p>
      </div>
    </header>
</template>
<script>
import util from './../../../../common/util.js'
import {mapState,mapGetters} from 'vuex'
export default{
	data(){
		return{
      testResults:{},
      time:''
		}
	},
  props:{
    info:null
  },
	mounted(){
	},
  computed:{
    ...mapState(['canvasMarks','currenttonetype']),
    ...mapGetters(['getTime']),
    userInfo:()=>{// 
      const productName = JSON.parse(sessionStorage.getItem('user_text'));
      try{
        if(productName.eq_left.string){
            return {
              eq_right:productName.eq_right.string,
              eq_left:productName.eq_left.string
            }
        }else{
          return {
              eq_right:'',
              eq_left:''
            }
        }
      } catch(err){
        alert('')
      }
    },
    user_Info:()=>{// 
      let user = JSON.parse(sessionStorage.getItem('user_info'))
      let birth = user['birth'];
      birth = util.dateToggle('','','',birth);
      return {'birth':birth,'name':user['user_name'],'gender':user['gender']};
    },
    // 
    current_gameName:()=>{
      let test_id = JSON.parse(sessionStorage.getItem('test_id'));
      return util.getType(test_id);
    },
  },
	methods:{

	}
}
</script>
<style lang ="less" scoped>
	/********/ 
</style>


