<template>
<div class="dangan">
    <div class="title">
        <h2></h2>
    </div>
    <div class="dangan-main clearfix">
        <div class="delete">
          <template>
            <el-button
              plain
              @click="dialogAddVisible"><i class="el-icon-plus"></i>
            </el-button>
          </template>
        </div>
        <div class="search">
            <el-input placeholder="" icon="search" v-model.lazy.trim="search" :on-icon-click="handleIconClick" @change="handleIconClick">
            </el-input>
        </div>
    </div>
    <div class="table" v-loading.body="loading">
        <template>
            <el-table
                :data="infoStudent"
                border
                ref="table"
                :height="height"
                :highlight-current-row="true"
                :stripe = "false"
                tooltip-effect="dark"
                style="width: 100%"
                @selection-change="handleSelectionChange"
                @row-click="infoList"
              >
              <el-table-column
                label=""
                type="index"
                width="auto"
                align="center"
                >
              </el-table-column>
              <el-table-column
                prop="name"
                label=""
                width="auto"
                align="center"
                :show-overflow-tooltip="true"
                >
                <!-- <template scope="scope">{{ scope.row.date }}</template> -->
              </el-table-column>
              <el-table-column
                prop="sex"
                label=""
                width="auto"
                align="center"
                >
              </el-table-column>
              <el-table-column
                prop="class_name"
                label=""
                width="auto"
                align="center"
                >
              </el-table-column>
            </el-table>
        </template>
    </div>
    <!--  -->
    <ele-add ref="add" v-on:newStudent="newData"></ele-add>
</div>
</template>
    
<script>
import addStudent from './add'
// 
import util from '../../../../common/util'
import code from '../../../../common/code.js'
import {showList,deleStudentInfo,showStudentInfo} from '../../../../api/api'
import {mapMutations,mapState} from 'vuex'
export default {
    // 
    components:{
        'ele-add' : addStudent
    },
    data (){
        return {
            check : false,
            selectArr:[],
            infoStudent:[],
            multipleSelection: [],
            search : '',
            // 
            infoBase :{},
            arr:'',
            loading:true,
            height:560
        }
    },
    created (){
      // 
      this.getList();
    },
    computed:{
        // id
        ...mapState(['hadDeleStudy']),
    },
    watch:{
        hadDeleStudy:function(){
            const deleId = this.hadDeleStudy[0];
            let otherStudentsInfo = this.infoStudent;
            otherStudentsInfo.forEach((item) =>{
                // id
                if(item.id === deleId){
                    util.arrayRemove();
                    otherStudentsInfo.remove(item);
                    this.infoList(otherStudentsInfo[0])//
                    // 
                    this.$refs.table.setCurrentRow(otherStudentsInfo[0]);
                }
            })
        }
    },
    methods : {
        // 
        getList(){
            var info = "name"
            var that = this
            this.$ajax.post(`/user/showlist`,info).then(function(response){
             console.log(response)
             // 
                if(response.code == 200){
                    that.infoStudent = response.data;
                    // 
                    that.arr = that.infoStudent.concat();
                    that.loading = false;
                    that.toPinName(that.arr);
                    var key = response.data[0].id;
                    setTimeout(()=>{//
                        that.$refs.table.toggleRowSelection(response.data[0]);
                        that.$refs.table.setCurrentRow(response.data[0]);
                    },50)
                    var str = {"id":key}
                    that.goToBase(key);
                    // 
                    that.getClickData(str);
                }else{
                     msgTipsErr(that,'!');
                }
            }).catch(function(err){
                msgTipsErr(that,'!');
            })    
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
         // 
        dialogAddVisible : function(){
            // console.log(1);
            this.$refs.add.showTable();
        },
        // 
        toPinName(str){
            let pinYinName = [];
            str.forEach((item) =>{
                pinYinName.push(code.toPinYin(item.name));
                item["pinyin"] = code.toPinYin(item.name);
            });
        },
        // 
        handleIconClick(ev){
            // 
            this.infoStudent = util.searchName(this.search,this.arr);
        },
        // 
        newData (str){
            // console.log(str);
            this.infoStudent = str;
            this.infoList(this.infoStudent[0])//
            this.$refs.table.setCurrentRow(this.infoStudent[0]);
        },
        infoList(row, event, column){
            // checked
            this.$refs.table.clearSelection();
            this.$refs.table.toggleRowSelection(row);
            let str = {"id":row.id}
            // let str = row.id
            let that = this;
            Vm.$emit('loading',true);
            // 
            this.getClickData(str);
            this.goToBase(row.id);
        },
        getClickData(str){
            this.$ajax.post(`/user/info`,str).then((response)=>{
                if(response.code === 200){
                    this.infoBase = response.data;
                    sessionStorage.setItem('user_info', JSON.stringify(response.data));
                    let test = this.infoBase;
                    return new Promise((resolve,reject)=>{
                        resolve(test)
                    })
                }else{
                    msgTipsErr(this,'!');
                }
            }).then((e)=>{
                // svg
                const id = new Array(e.user_id);
                this.$ajax.post('/info/getaudiograms',{user_id:id,type_id:"6"}).then((res)=>{
                    let svgData =  new Object;
                    if(res.code === 200 ){
                        if(res.data.users){//
                            svgData = res.data.users[0];
                            this.$store.commit('hasCanvasData',res.data.users[0]);
                        }else{//
                            this.$store.commit('hasCanvasData',{});
                        }
                        // 
                        Vm.$emit('change',e,svgData);
                    }else{
                        msgTipsErr(this,'!');
                    }
                })
            }).catch(function(error){
                alert(error);
            });
        },
        goToBase(str){
            //
            this.$router.push({path:'/home/Archives/dalist/base',query:{"id":str}});
            // id
            sessionStorage.setItem('user_id', JSON.stringify(str));
            // vuex
            this.$store.commit('goToBase',{'base':'base','id':str});
        }
    }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style  lang="less">
    .dangan{
        height:100%;
        .title{
            border-bottom:1px solid #e7eaec;
            h2{
             margin-left:20px;
             line-height: 70px;
            }
        }
        .table{
            padding:10px;
            table{
              width:100%;
            }
            .el-table__body-wrapper{/**********/
                overflow-x: hidden;
            }
            .el-table .cell, .el-table th>div{padding-left:5px;padding-right:5px;}
        }
        .dangan-main{
            padding:10px!important;
            .delete{
                float: left;
            }
            .search{
                float:right;
                width:60%;
            }
            .el-button{
                margin-left:-2px;
                height: 36px;
                min-width:70px !important;
                width:70px !important;
            }
        }
        .el-table_1_column_10{
            overflow: hidden; /**/
            text-overflow: ellipsis;/**/
            white-space: nowrap;/**/
        } 
    }
    // 
    div.el-message{
        top:50%;
        min-width:150px;
        background-color: rgba(0,0,0,0.3);
        p{
            color:#fff;
        }
    }
</style>



