<template>
  <div id="app">
     <router-view></router-view>
  </div>
</template>

<script>
window.isToggle = true;
window.adIsOnline = true;
// 
import {mapMutations,mapState} from 'vuex'
import util from './common/util.js'
export default {
  name: 'app',
  // 
  data() {
    return {
      
    }
  },
  created (){
    console.log(1)
    // websocket
    this.connect_websocket();
  },
  computed:{
    ...mapState(['wepAppTime']),
  },
  methods : {
    // 
    hasCal_data(str){
      try{
        if(str.mesfrom === "sper"){
          if(str.params.code){
            Common.sperErr(str.params.code,this).then((e)=>{
              Vm.$emit('closeVioce',true);
            }).catch((err)=>{
              Vm.$emit('closeVioce',true);
            })
          }
        }
        // if(str['mescon'] === 'err_sendmes'){
        //   if(102){
        //     Common.sperErr(102,this).then((e)=>{
        //       Vm.$emit('closeVioce',true);
        //     }).catch((err)=>{
        //       Vm.$emit('closeVioce',true);
        //     })
        //   }
        // }
        // 
        if(str.mesfrom === "sper" && str.mescon === "cal_end"){
          this.$store.commit('progress_fn',str.params);
        };
        if(str['mescon'] === 'ad_regist_suss' || str['mescon'] === 'cal_finish'){
          let obj = str['params']['cal_data'];
          // this.$store.commit('calibrationData',maxDb);
          this.$store.commit('inint_calibrationData',obj);
        }
      }catch(err){
        throw Error(err)
      }
    },
    tese_data(str){
      if(str['mescon'] === "rtspl"){
        this.$store.commit('test_spl',str.params['spl']);
      }
    },
    connect_websocket(){
      let that =this;
      (function (){
        if (!window.WebSocket) {
         alert("websocket");
           return false;
        }
        let wsServer = 'ws://'+url + '/ad'; //
        window.websocket = new WebSocket(wsServer); //WebSocket
        websocket.onopen = function (evt) {//
          console.log("webcoket ")
          var date = new Date();
          var reg={
              mesid:parseInt((date.getTime())),
              mestype:'cmd',
              mesfrom:'ad',
              mesto:'server',
              mescon:'regist'
            }
          websocket.send(JSON.stringify(reg));//
        };
        websocket.onclose = function (evt) {
          //
          console.log("websocket");
        };
        
        websocket.onmessage = function (evt) {
          //evt.data
          const websocketData = JSON.parse(evt.data);
          console.log(websocketData);
          that.hasCal_data(websocketData)
          that.tese_data(websocketData)//
          // 
          var isOnline = true;
          if(websocketData.mesfrom === "ctld"){
            Vm.$emit('wsmsg',websocketData);
          }
          let time = that.$store.state.time;
          if(websocketData.mesfrom === "uploader"){//
            that.$store.commit('getter_time',websocketData);
              that.wskt.hasCurrentPage(that,time,websocketData);//
          }
          // '', '',
          if(websocketData.mesfrom === "server"){
            try{
              if(websocketData.mescon === "ctld_regist_suss"){
                isOnline = true;
                window.isToggle = true;
              } else if(websocketData.mescon === "ctld_disconnect"){
                isOnline = false;
              }else if(websocketData.mescon === "ad is not null,you cannot regist"){
                window.adIsOnline = false;
                setTimeout(()=>{
                  alert(",1001");
                },0);
                return;
              } else if(websocketData.mescon === "ad_regist_suss"){
                window.adIsOnline = true;
                // 
                 // msgTipsSuccess(that,'websocket');
                 return;
              }
              that.$store.commit('CMD_IS_ONLINE',{'local':isOnline,'global':window.isToggle});
              // console.log(websocketData.mescon)
              Vm.$emit('servermsg',websocketData,isOnline,window.isToggle);
            } catch(err){
              throw Error(err)
            }
          };
          // console.log(window.isToggle,isOnline)
          // 
          // if(websocketData.params['memo'] == "target is not online" ){
          //   if(window.isToggle && isOnline){return;}
          //   Vm.$emit('target',websocketData);
          //   // alert("")
          //   that.$alert('!', '', {
          //       confirmButtonText: '',
          //       }).then(() => {
          //         that.$router.push({ path: '/home' });
          //       });
          // }

          return;
          // console.log(websocket.readyState);
        };
        websocket.onerror = function (evt) {
          //
          throw Error("websocket ")
        };

      })();
    }

  }
}
</script>

<style lang="less">
#app{
  background-color:#53c7a0;
  margin:0 auto;
  height: 100%;
  /*overflow: auto;*/
  position: relative;
}
/***/
div.el-message.msgtip{
    background-color: #999;
  }

</style>



